.avatar {
  overflow: hidden;
  border-radius: 50%;
}
.avatar img {
  width: auto;
  height: 100%;
  margin: 0 auto;
}
.avatarWrap {
  position: relative;
}
.small {
  width: 25px;
  height: 25px;
}
.medium {
  width: 50px;
  height: 50px;
}
.large {
  width: 132px;
  height: 132px;
}
